@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* Navbar */

nav {
	display: flex;
	background: linear-gradient(
		59deg,
		rgba(102, 45, 140, 1) 35%,
		rgba(237, 30, 121, 1) 100%
	);
	align-items: center;
	padding: 15px 40px;
	margin-bottom: 17px;
}

.logo {
	flex-basis: 33%;
}
.logo span {
	font-size: 1.7rem;
	color: white;
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.logo a {
	text-decoration: none;
	color: white;
}

.logo a {
	text-decoration: none;
	color: white;
	font-size: 1.4rem;
	margin-left: 30px;
}

.search a {
	text-decoration: none;
	color: white;
	font-size: 1.1rem;
	margin-left: 30px;
	display: inline-block;
}

.search {
	flex-basis: 67%;
}

.search input {
	background-color: rgba(255, 255, 255, 0.2);
	font-size: 20px;
	font-family: "ubuntu", sans-serif;
	height: 35px;
	width: 50%;
	min-width: 300px;
	border: none;
	border-radius: 7px;
	padding: 8px;
	outline: none;
	opacity: 80%;
	transition: all 0.3s ease-in-out;
}

.search input:focus {
	opacity: 100%;
	background-color: white;
	transition: all 0.3s ease-in-out;
}

/* 
	Media Query
*/

@media only screen and (max-width: 700px) {
	nav {
		flex-direction: column;
		padding: 15px 0;
	}
	.logo {
		flex-basis: 100%;
		margin-bottom: 17px;
	}
	.search {
		flex-basis: 100%;
	}
}

.input-container {
	width: 100%;
}

.input-container form {
	position: relative;
	display: flex;
	flex-direction: column;
	margin-top: 35px;
	box-shadow: 0 0 7px rgba(0, 0, 0, 0.3);
	border-radius: 15px;
	width: 420px;
	margin: auto;
}

.input-container form input {
	font-family: "ubuntu", sans-serif;
	font-weight: bold;
	width: 100%;
	font-size: 27px;
	padding: 12px 7px;
	border: none;
	outline: none;
	border-radius: 15px;
}

.input-container form textarea {
	font-family: "ubuntu", sans-serif;
	width: 100%;
	font-size: 16px;
	padding: 12px 7px;
	border: none;
	outline: none;
	resize: none;
	border-radius: 15px;
}

.create-error {
	text-align: center;
	color: red;
	font-family: "ubuntu", sans-serif;
	padding: 7px 0;
}

.input-container form button {
	background-color: white;
	color: rgb(102, 45, 140);
	font-size: 30px;
	border: none;
	outline: none;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	position: absolute;
	right: 10px;
	bottom: -29px;
	box-shadow: 0 0 7px rgba(0, 0, 0, 0.3);
	transition: all 0.3s ease-in-out;
}

.input-container form button:hover {
	background: linear-gradient(
		59deg,
		rgba(102, 45, 140, 1) 35%,
		rgba(237, 30, 121, 1) 100%
	);
	color: white;
}

.input-container .MuiSvgIcon-root {
	font-size: 2.5rem !important;
}

/* 
    Cards Style
*/

.cards-container {
	width: 100%;
	padding: 70px 50px;
}

.card {
	width: 350px;
	border: 1px solid rgba(0, 0, 0, 0.2);
	border-radius: 15px;
	font-family: "ubuntu", sans-serif;
	display: inline-block;
	margin: 20px 20px;
	position: relative;
}

.card:hover {
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
}

.card h2 {
	font-size: 1.2rem;
	font-weight: bold;
	padding: 15px 13px;
}

.card p {
	padding: 0 13px 15px 13px;
}

/* 
Card Buttons
*/

.card:hover .card-btn button,
.card:hover .pinned-btn {
	visibility: visible;
}

.card-btn button {
	visibility: hidden;
	padding-top: 10px;
	width: 50%;
	transition: visibility 0.3s ease-in-out;
}

.edit-btn:hover .MuiSvgIcon-root {
	color: royalblue;
}

.del-btn:hover .MuiSvgIcon-root {
	color: red;
}

/* 
Pin Buttons
*/

.pinned-btn {
	visibility: hidden;
	position: absolute;
	top: 15px;
	right: 13px;
	cursor: pointer;
	transition: visibility 0.3s ease-in-out !important;
}

.pinned-btn:active {
	width: 2rem;
	height: 2rem;
	transition: all 0.2s ease-in-out;
}

#pinned-title,
#others-title {
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	font-size: 14px;
	color: #5f6368;
	margin-left: 30px;
}

#others-title {
	margin-top: 20px;
}

