/* 
    Cards Style
*/

.cards-container {
	width: 100%;
	padding: 70px 50px;
}

.card {
	width: 350px;
	border: 1px solid rgba(0, 0, 0, 0.2);
	border-radius: 15px;
	font-family: "ubuntu", sans-serif;
	display: inline-block;
	margin: 20px 20px;
	position: relative;
}

.card:hover {
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
}

.card h2 {
	font-size: 1.2rem;
	font-weight: bold;
	padding: 15px 13px;
}

.card p {
	padding: 0 13px 15px 13px;
}

/* 
Card Buttons
*/

.card:hover .card-btn button,
.card:hover .pinned-btn {
	visibility: visible;
}

.card-btn button {
	visibility: hidden;
	padding-top: 10px;
	width: 50%;
	transition: visibility 0.3s ease-in-out;
}

.edit-btn:hover .MuiSvgIcon-root {
	color: royalblue;
}

.del-btn:hover .MuiSvgIcon-root {
	color: red;
}

/* 
Pin Buttons
*/

.pinned-btn {
	visibility: hidden;
	position: absolute;
	top: 15px;
	right: 13px;
	cursor: pointer;
	transition: visibility 0.3s ease-in-out !important;
}

.pinned-btn:active {
	width: 2rem;
	height: 2rem;
	transition: all 0.2s ease-in-out;
}

#pinned-title,
#others-title {
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	font-size: 14px;
	color: #5f6368;
	margin-left: 30px;
}

#others-title {
	margin-top: 20px;
}
